$primary: #E98900;
.pcollector-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color:rgba(0,0,0,.4);
  z-index: 9999;
}
.pcollector-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffefd8;
  width: 570px;
  border-radius: 10px;
  .panel-close-btn {
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100% 100%;
    width: 47px;
    height: 47px;
    display: block;
    position: absolute;
    right: -23px;
    top: -25px;
  }
}
.panel-header {
  background-color: $primary;
  font-size:16px;
  color:#fff;
  padding:8px 20px;
  border-radius: 10px 10px 0 0;
}
.panel-body {
  padding: 10px 20px;
  .title {
    font-size: 14px;
    color: #aaa;
    margin-bottom: 5px;
  }
  .picture-box {
    margin-bottom: 10px;
    .picture-list-box {
      overflow: auto;
      height: 174px;
      .picture-list {
        overflow: hidden;
        padding-bottom: 12px;
        .picture-item {
          padding: 12px 12px 0;
          overflow: hidden;
          width: 100%;
          .item-img {
            float: left;
            height: 150px;
            width: 150px;
            display: block;
            position: relative;
            padding: 10px;
            background: url("data:image/png;base64,") no-repeat;
            background-size: 100% 100%;
            .img-container {
              width: 100% !important;
              height: 100% !important;
              background-color: #fff;
              padding: 5px;
              box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.2);
            }
            img {
              background-position: center;
              background-repeat: no-repeat;
              display: block;
              background-size: 100% auto;
              &.e {
                width: 100%;
                height: 100%;
              }
              &.h {
                background-size: auto 100%;
              }
            }
          }
          .item-content {
            float: left;
            display: block;
            width: 346px;
            height: 150px;
            padding: 10px 0 10px 20px;
            textarea {
              resize: none;
              width: 100%;
              height: 100%;
              background-color: white;
              border-radius: 10px;
              padding: 15px;
              overflow: auto;
              outline: none;
              word-wrap: break-word;
              border: 0;
            }
          }
        }
      }
    }
  }
  .album-box {
    .create-album {
      height: 40px;
      position: relative;
      border-radius: 10px;
      overflow: hidden;
      margin-bottom: 10px;
      input {
        border: 0;
        width: 100%;
        height: 40px;
        padding: 0 110px 0 20px;
        outline: none;
      }
      button {
        position: absolute;
        right: 0;
        top: 0;
        width: 100px;
        color: white;
        background-color: $primary;
        border: 0;
        height: 40px;
        line-height: 40px;
        outline: none;
        &:hover {
          background-color: #CD6C00;
        }
      }
    }
    .album-list-box {
      position: relative;
      background-color: white;
      padding: 63px 12px 10px;
      border-radius: 10px;
      height: 208px;
      .active-album-name {
        margin: 0 15px;
        width: 500px;
        height: 50px;
        position: absolute;
        top: 0;
        left: 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        padding: 22px 10px 10px 10px;
        color: #aaa;
        user-select: none;
      }
      .list-container {
        height: 135px;
        overflow: auto;
        padding-left: 4px;
      }
      .album-list {
        overflow: hidden;
        width: 100%;
        .album-item {
          float: left;
          width: 110px;
          margin: 16px 18px 0 0;
          position: relative;
          cursor: pointer;
          user-select: none;
          &:nth-child(1),
          &:nth-child(2),
          &:nth-child(3),
          &:nth-child(4) {
            margin-top: 0;
          }
          &:nth-child(4n) {
            margin-right: 0;
          }
          &::after {
            content: attr(data-total);
            width: 30px;
            height: 30px;
            background-color: $primary;
            color: white;
            display: block;
            position: absolute;
            bottom: 25px;
            right: 0;
            text-align: center;
            line-height: 30px;
            font-size: 14px;
            z-index: 5555;
          }
          .img-container {
            display: block;
            height: 110px;
            width: 110px;
            padding: 5px;
            position: relative;
            background-color: #ffffff;
            border: 1px solid #d9d9d9;
            img {
              background-position: center;
              background-repeat: no-repeat;
              display: block;
              background-size: 100% auto;
              &.e {
                width: 100%;
                height: 100%;
              }
              &.h {
                background-size: auto 100%;
              }
            }
            &::after {
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              content: " ";
              background: url("data:image/png;base64,") no-repeat 50% 50% rgba(0, 0, 0, 0.2);
              background-size: 40px 40px;
              z-index: 5;
              display: none;
            }
          }
          &.yez-active .img-container::after {
            display: block;
          }
          span {
            display: block;
            margin-top: 5px;
            width: 100%;
            text-align: center;
            font-size: 14px;
            line-height: 16px;
            height: 19px;
            color: #aaa;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
    }
  }
}
.panel-footer {
  text-align: center;
  padding-bottom: 10px;
  button {
    outline: none;
    color: white;
    border: 0;
    border-radius: 10px;
    height: 50px;
    line-height: 49px;
    padding: 0 16px 0 65px;
    background: url("data:image/png;base64,") no-repeat 15px 8px $primary;
    background-size: 41px 32px;
    &:hover {
      background-color: #CD6C00;
    }
  }
}
